.emailNotCame {
  margin-top: 1rem;
  text-align: center;
}

.goToEmail {
  padding: 1rem 1.2rem;
  min-width: 8rem;
  width: 80%;
  margin: 0 auto;
  text-align: center;
  border-radius: 2rem;
  border: none;
  text-decoration: none;
  color: white;
  cursor: pointer;
  transition: 0.3s all ease-in;
  background-color: var(--primary-color);
}

.goToEmail:hover {
  background-color: #254dd1;
}
